.inputs-block {
  display: flex;
  flex-direction: column;
  background: #ffffff;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  width: 230px;
  height: 250px;
  margin-left: auto;
  margin-right: 20px;
  color: #4a4a4a;
  align-content: flex-start;
}

.checkbox-heading {
  font-family: Open Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 12px;
  letter-spacing: 0.5px;
  margin: 10px;
  margin-left: 20px;
  text-transform: uppercase;
  text-align: left;
  color: #4a4a4a;
}

.checkbox-wrapper {
  display: flex;
  text-align: left;
  background: #ffffff;
  padding: auto;
  flex: 1 1 auto;
  align-self: flex-start;
  width: 100%;
  max-height: 40px;
  line-height: 20px;
}

.checkbox-wrapper:hover {
  background: #f1fcff;
  cursor: pointer;
}

.checkbox {
  flex: 0 1 auto;
  align-self: center;
  margin-left: 20px;
  margin-right: 10px;
}

.checkbox:hover {
  background: #f1fcff;
  cursor: pointer;
}

input[type='checkbox'] {
  transform: scale(1.5);
}

.checkbox-label {
  font-family: Open Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 20px;
  flex: 1 1 auto;
  align-self: center;
  text-align: left;
  color: #4a4a4a;
}

.checkbox-label:hover {
  background: #f1fcff;
  cursor: pointer;
}

.form-checkbox {
  text-align: center;
}
