@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Open Sans, Arial, Helvetica, sans-serif;
}

/* Сброс стилей у button */
button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}

.wrapper {
  background: #f3f7fa;
  text-align: center;
}

.app {
  position: relative;
}

.main {
  display: flex;
  text-align: center;
  padding-bottom: 20px;
}

.left-aside {
  flex: 1 1 auto;
  align-self: flex-start;
  width: 230px;
  height: 250px;
}

.right-aside {
  width: 500px;
  flex: 1 1 auto;
  align-self: flex-start;
}

.header-wrapper {
    height: 160px;
    padding-top: 50px;
}

.logo {
    width: 70px;
    height: 70px;
    margin-right: auto;
    margin-left: auto;
    background-image: url(/aviasales_test_task/static/media/Logo.d2a4807b.png);
    background-position: center;
}
.inputs-block {
  display: flex;
  flex-direction: column;
  background: #ffffff;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  width: 230px;
  height: 250px;
  margin-left: auto;
  margin-right: 20px;
  color: #4a4a4a;
  align-content: flex-start;
}

.checkbox-heading {
  font-family: Open Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 12px;
  letter-spacing: 0.5px;
  margin: 10px;
  margin-left: 20px;
  text-transform: uppercase;
  text-align: left;
  color: #4a4a4a;
}

.checkbox-wrapper {
  display: flex;
  text-align: left;
  background: #ffffff;
  padding: auto;
  flex: 1 1 auto;
  align-self: flex-start;
  width: 100%;
  max-height: 40px;
  line-height: 20px;
}

.checkbox-wrapper:hover {
  background: #f1fcff;
  cursor: pointer;
}

.checkbox {
  flex: 0 1 auto;
  align-self: center;
  margin-left: 20px;
  margin-right: 10px;
}

.checkbox:hover {
  background: #f1fcff;
  cursor: pointer;
}

input[type='checkbox'] {
  transform: scale(1.5);
}

.checkbox-label {
  font-family: Open Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 20px;
  flex: 1 1 auto;
  align-self: center;
  text-align: left;
  color: #4a4a4a;
}

.checkbox-label:hover {
  background: #f1fcff;
  cursor: pointer;
}

.form-checkbox {
  text-align: center;
}

.main-window__wrapper {
  display: flex;
  flex-direction: column;
  width: 500px;
}

.main-window__btn-container {
  width: 500px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.main-window__tickets-container {
  width: 500px;
  display: flex;
  flex-direction: column;
}

.ticket {
  width: 500px;
  height: 180px;
  background: #ffffff;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  margin-bottom: 20px;
}

.btn {
  display: inline-block;
  text-align: center;
  text-decoration: none;
  margin: 0;
  font-family: Open Sans;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 20px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  width: 250px;
  height: 50px;
  padding: 15px;
  border: 1px solid #dfe5ec;
  background: #ffffff;
  color: #4a4a4a;
}

.btn.btn-active {
  color: #ffffff;
  background: #2196f3;
  border: 1px solid #2196f3;
}

.btn-left {
  border-radius: 5px 0 0 5px;
}

.btn-right {
  border-radius: 0 5px 5px 0;
}

.btn:active {
  transform: translateY(1px);
  -webkit-filter: saturate(150%);
          filter: saturate(150%);
}

